import React from "react";
import "../../styles/licensing.css";
import LicensingStar from "../../icons/stars/licensing_star.svg";
import Dollar from "../../icons/currency/USDollar.svg";

function LicensingCardThree() {
  return (
    <div
      className="licensing-card-container"
      style={{ backgroundColor: "#F4F5F6", borderRadius: "24px" }}
    >
      <div className="licensing-tag">
        <div className="licensing-tag-star">
          <img src={LicensingStar} alt="Star" />
        </div>
        <div className="licensing-tag-tier">Tier One tag; 20 char</div>
      </div>
      <div className="licensing-card-subhead">Subhead 20 char</div>
      <div className="licensing-card-head">Headline 20 char</div>
      <div className="licensing-card-price">
        <div className="price">
          <div className="price-divider"></div>
          <div className="price-container">
            <div className="price-dollar">
              <img src={Dollar} alt="dollar" />
            </div>
            <div className="price-integer">24</div>
            <div className="price-right">
              <div className="price-decimal">95</div>
              <div className="price-period">/month</div>
            </div>
          </div>
        </div>
        <button className="price-btn">Get started</button>
      </div>
      <p className="licensing-card-content">
        Our most popular subscription. Recommended for multiple data centers
        and/or clouds. Includes all the features in Data Center Networking
        Essentials plus advanced capabilities.
      </p>
    </div>
  );
}

export default LicensingCardThree;



